
import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import IndexRow from './IndexRow';
import Dexie from 'dexie';
import LibTask from '../../libs/LibTask';
import LibDexie from '../../libs/LibDexie';
import HeadTest from '../Layouts/HeadTest';
//
class TestChild extends Component {
    constructor(props) {
        super(props);
//        this.state = {data: ''}
        this.state = {
            data: [], 
            data_one: null, 
            param:{id: 1 } ,
        }
        this.db = null
    }
    componentDidMount(){
        var config = LibTask.get_const()
        this.db = new Dexie( config.DB_NAME );
        this.db.version(config.DB_VERSION).stores( config.DB_STORE );
        this.get_items()        
    }
    get_items(){
        var self = this
        this.db.tasks.toArray().then(function (items ) {
            var tasks = LibDexie.get_reverse_items(items)
            self.setState({ data: tasks })
// console.log( tasks.length )
            if(tasks.length > 0){
                self.setState({ data_one: [tasks[0]] })
            }
        });
    }
    tabRow(){
        if(this.state.data instanceof Array){
            return this.state.data.map(function(object, i){
                return <IndexRow obj={object} key={i} />
            })
        }
    }
    displayHead(){
        if(this.state.data instanceof Array){
            return <HeadTest obj={this.state.data_one} />
        }
    }
    render(){
        return (
        <div className="container">
            {this.displayHead()}
            <hr />
            <h3>Task - testChild</h3>
            <div className="row">
                <div className="col-md-6">
                    <Link to="/task_create"
                     className="btn btn-sm btn-primary">+ Create
                    </Link>
                </div>
                <div className="col-md-6">
                </div>
            </div><br />
            <table className="table table-hover">
                <thead>
                <tr>
                    <th>ID</th>
                    <th>Title</th>
                    <th>Conent</th>
                    <th>Actions</th>
                </tr>
                </thead>
                <tbody>
                    {this.tabRow()}
                </tbody>
            </table>
            <hr />

        </div>
        )
    }
}
export default TestChild;

