
import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import Dexie from 'dexie';
import LibTodo from '../../libs/LibTodo';

//
class Edit extends Component {
    constructor(props) {
        super(props);
//        this.state = {title: '', content: ''};
        this.state = {
            title: '', content: '',
            type: 0, complete_btn_name: "完了登録",
        };
        this.id = 0
        this.handleClick = this.handleClick.bind(this);
        this.handleClickDelete = this.handleClickDelete.bind(this);
        this.handleClickComplete = this.handleClickComplete.bind(this);
        this.db = null
    }
    componentDidMount(){
        this.id  = parseInt(this.props.match.params.id)
        var config = LibTodo.get_const()
        this.db = new Dexie( config.DB_NAME );
        this.db.version(config.DB_VERSION).stores( config.DB_STORE );         
console.log( this.id);
        this.get_item( this.id )
    }
    async get_item(id){
        const item = await this.db.todos.get(id);
        var btn_name = "完了登録"
        var complete_type = 0
        if(item.complete === 1){
            btn_name = "未完に戻す"
            complete_type = 1
        }
        this.setState({ 
            title: item.title, 
            content: item.content,
            complete_btn_name: btn_name,
            type: complete_type
        });        
        console.log(item);                          
//        console.log(item);                          
    }
    update_item(){
        this.db.todos.update(parseInt( this.id ) , {
            title: this.state.title,
            content: this.state.content,
        });
        this.props.history.push("/todo");
    }    
    handleClickComplete(){
//console.log( this.state.type )
        var value= 0
        if(this.state.type === 0){
            value = 1
        }
        this.db.todos.update(parseInt( this.id ) , {
            complete: value,
        });
        this.props.history.push("/todo");
    }
    handleClickDelete(){
//        console.log("#-handleClickDelete")
        this.db.todos.delete(parseInt(this.id) );
        this.props.history.push("/todo");
    }
    handleClick(){
        console.log("#-handleClick")
        this.update_item()
//        console.log( this.state )
    }        
    handleChangeTitle(e){
        this.setState({ title: e.target.value })
    }
    handleChangeContent(e){
        this.setState({ content: e.target.value })
    }
    render(){
        return (
        <div className="container">
            <Link to="/todo" className="btn btn-outline-primary mt-2">Back</Link>
            <hr className="mt-2 mb-2" />            
            <h1>Todo - Edit</h1>
            <div className="row">
                <div className="col-md-10"></div>
                <div className="col-md-2">
                </div>
            </div>
            <div className="form-group">
                <label>Title</label>
                <div className="col-sm-6">
                    <input type="text" className="form-control"  value={this.state.title}
                    onChange={this.handleChangeTitle.bind(this)} />
                </div>
            </div>
            <div className="form-group">
                <label>Content:</label>
                <div className="col-sm-10">
                    <textarea className="form-control" value={this.state.content}
                    rows="10"
                    onChange={this.handleChangeContent.bind(this)} ></textarea>
                </div>
            </div>
            <div className="form-group">
                <button className="btn btn-primary" onClick={this.handleClick}>Save
                </button>
            </div>
            <hr />
            <button className="btn btn-outline-success btn-sm mt-2"
            onClick={this.handleClickComplete}>
                {this.state.complete_btn_name}

            </button>
            <hr />
            <div className="form-group">
                <button className="btn btn-outline-danger btn-sm mt-2"
                onClick={this.handleClickDelete}>Delete
                </button>
            </div>

        </div>
        )
    }
}
export default Edit;